header {
    border-bottom: 1px solid rgb(170, 170, 170);

}

.hide {
    visibility: hidden;
}

.transform-1 {
    width: 35px;
    transform: translateY(9px) rotate(45deg);
    -webkit-transform: translateY(9px) rotate(45deg);
    -moz-transform: translateY(9px) rotate(45deg);
    -ms-transform: translateY(9px) rotate(45deg);
    -o-transform: translateY(9px) rotate(45deg);
}

.transform-2 {
    width: 35px;
    transform: translateY(-10px) rotate(-45deg);
    -webkit-transform: translateY(-10px) rotate(-45deg);
    -moz-transform: translateY(-10px) rotate(-45deg);
    -ms-transform: translateY(-10px) rotate(-45deg);
    -o-transform: translateY(-10px) rotate(-45deg);
}

.menu .p1 {
    width: 30px;
    height: 2px;
    margin-bottom: 7px;
    background-color: black;
    transition: 0.2s;
    -webkit-transition: 0.2s;
}

.menu .p2 {
    height: 2px;
    width: 38px;
    margin-bottom: 7px;
    transition: 0.2s;
    background-color: black;
    -webkit-transition: 0.2s;
}
.register {
    width: 335px;
    height: fit-content;
    padding-bottom: 1rem;
}

.inputs input {
    border: 1px solid #DDD;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
    width: 95%;
    margin-bottom: 1rem;

}

.inputs h2 {
    margin-bottom: 5px;
    color: #929191;
    font-size: 1.1rem;
}

.btns-login {
    display: flex;
    gap: 1.5rem;
}

.btns-login button {
    background-color: #4b8ef1;
    color: white;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    border-radius: 5px;
    transition: 0.8s;
}

.btns-login button:hover {
    background-color: #DDD;
    color: black;
}
@media (max-width:767px) {

        .links li a{
            padding-top: 1rem;
            padding-bottom: 1rem;
            display: inline-block;
            width: 100%;
            
        }
    .links li:not(:nth-of-type(6)) {
            border-bottom: 1px solid rgb(200, 200, 200);
            padding: 0;
        }
        
}
@layer{
    .links {
            transition: 0.8s ease-in-out;
            @apply max-md:max-h-0;
        }
    
        .links li {
            @apply max-md:py-4 border border-gray-300 max-md:ml-0 py-10 max-lg:ml-8 ml-12 max-md:w-full max-md:text-center max-md:bg-[#f7f7f7] max-md:cursor-pointer max-md:hover:bg-[#e7e7e7];
    
        }

        .links > li > a {
            @apply font-medium text-[1.1rem];
        }
        .links.show {
            @apply max-md:max-h-[400%];
        }
}