
.about::before{
    content: "";
    position: absolute;
    background-image: url("../../images/about-bg.jpg");
    background-repeat: no-repeat;
        background-size: contain;
        width: 58%;
        height: 165vh;
        z-index: -1;
        top: -22rem;
        right: 0rem;
    
}

.feat-row{
    display: flex;
    gap: 3rem;
    
}
.about .container{
    display: flex;
}
.features{
    display: flex;
    flex-wrap: wrap;
}


.features .feature{
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    border-radius: 5rem;
    padding: 1rem;
    min-width: 250px;
    margin-bottom: 2rem;
}

.features .feature h2{
    font-weight: bold;
    font-size: 1.2rem;
    color: #2a2a2a;
}
.features .feature h2:hover{
    color: #4b8ef1;
    cursor: pointer;
}
.features .feature p{
    color: #afafaf; 
}
@media (max-width:991px) {
    .about::before {
        display: none;
    }
        .features .feature {
            margin-bottom: 1rem;
        text-align: center;
        width: 85%;
        margin-inline:auto;
        }
        .feat-row{
            margin-bottom: 1rem;
            gap: 1rem;
        }
}