@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base{
    html{
        scroll-behavior: smooth;
    }
    .header{
        text-align: center;
        width:auto;
        margin-inline:auto;
    }
.header .text .main-h2{
    font-size: 1.7rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    position: relative;
}
.header .text .main-h2::before,.header .main-h2::after{
    content: "";
    position: absolute;
    width: 30px;
    height: 2px;
    background-color: #4b8ef1;
    bottom:-0.7rem;
    
}
.main-btn{
    background: linear-gradient(105deg, rgba(91, 104, 235, 1) 0%, rgba(40, 225, 253, 1) 100%);
}
