left: 50.5%;
}
.header .main-h2::after{
right: 50.5%;
} */
.header .text .main-h2 span{
    color: #4b8ef1;
}
.header .text p {
    color: #afafaf;
}
}
::selection{
    color: white;
    background-color: #4b8ef1;
}
