.pricing{
    padding-top: 3rem;
    padding-bottom: 3rem;
}
.plans{
    display: flex;
    gap: 3rem;
}
.plans .plan{
display: flex;
justify-content: space-around;
align-items: center;
flex-direction: column;
flex: 1;
position: relative;
    height: 85vh;
}
.plans .plan span{
    position: absolute;
    top: 2rem;
    left:3rem;
    color: white;
    font-weight: 700;
    font-size: 2rem;
}
.plans .plan h2{
    font-weight: 600;
    font-size: 1.3rem;
    margin-bottom: 2rem;
}
.plans .plan button{
    @apply border border-solid rounded-[3rem] mb-12 font-semibold text-[#4b8ef1] hover:bg-[#4b8ef1] hover:text-white px-5 py-2 border-[#4b8ef1];
    transition: 0.8s all;
}
.plans .plan:not(:nth-last-of-type(2)){

background-image: url("../../images/regular-table-top.png"),url(
    "../../images/regular-table-bottom.png"
);
background-repeat: no-repeat;
background-position: top left, bottom right;
border-radius: 4rem;
border-radius: 4rem;
border: 1px solid rgba(0, 0, 0, 0.1);
}
.plan-features p{
margin-bottom: 0.6rem;
}
.plans .plan:nth-of-type(2){
background-image: url("../../images/pro-table-top.png"), url("../../images/pro-table-bottom.png"
        );
    background-repeat: no-repeat;
    background-position: top left, bottom right;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4rem;
    border-radius: 4rem;
    scale: 1.1;
}
@media (max-width:991px) {
        .plans .plan{
            margin-inline:auto;
            width: 90%;
        }
    .plans .plan:nth-last-of-type(2){
        scale: 1;
    }
}