
.home{

        padding-top: 10rem;
        @apply max-lg:pt-[16rem] ;
            /* overflow: hidden; */
}
.home::before {
    content: "";
    position: absolute;
    background-image: url("../../images/slider-left-dec.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    transition: 0.8s;
    opacity: 0;
    @apply max-lg:hidden;
}
.home.show::before{
    opacity: 1;
}
.home .container .first-half{
    transform: translateX(-40rem);
    transition: 0.8s;
}
.home .container .first-half.show{
    transform: translateX(0);
}
.home .container .second-half{
    transform: translateX(40rem);
    transition: 0.8s;
}
.home .container .second-half.show{
    transform: translateX(0);
}