footer{
    background-image: url("../../images/footer-bg.png");
    background-repeat: no-repeat;
    /* height: 100vh; */
    background-position: center top;
    padding-top: 15rem;
    padding-bottom:4rem;
    overflow: hidden;
}
footer .foot-head h2{
    line-height: 1.5;
}
footer .container > div{
line-height: 2.3;
}
footer .container>div h2{
font-weight: 700;
font-size: 1.4rem;
line-height: 3.5;
}
footer .container>div ul li{
    font-size: 1.1rem;
    font-weight: 400;
}
footer .container>div ul li:hover{
    color: rgb(222, 222, 222);
    cursor: pointer;
}
footer .container>div:nth-child(1) p{
    line-height: 3;
font-size: 1.1rem;
}
footer .container>div:nth-child(1) p:hover {
        color: rgb(222, 222, 222);
        cursor: pointer;
}
@media (max-width:1199px) {
    footer{
        background-size: cover;
    }

}