.services{
    margin-bottom: 15rem;
}
.services::after,
.services::before {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;

}
.services::before{

    background-image: url("../../images/services-right-dec.png");
    background-position: top right;
        top: -1rem;
}

.services::after{
    content: "";
    position: absolute;
    background-image: url("../../images/services-left-dec.png");
    background-position: bottom  left;
    bottom: -19rem;
}
.container .service{

    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    padding: 30px;
    border-radius: 10px;
    background-color: #fff;
    border-top-right-radius: 3rem;
    flex: 1;
    transition: 0.8s ;
position: relative;
z-index: 1;
@apply max-lg:text-center max-lg:w-[95%] max-lg:mx-auto;

}

.container .service:hover{
    
    background-image: url("../../images/service-bg.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: top right;}
.container .service:hover .text,.container .service:hover .text p,.container .service:hover button{
    color: white;
}


.container .service img{
    margin-bottom: 1.5rem;
    @apply max-lg:mx-auto;
}
.container .service .text h2{
    font-weight: bold;
    font-size: 1.2rem;
    margin-bottom: 1rem;
}
.container .service .text p{
    margin-bottom: 1rem;
}
.container .service button i{
    transition: 0.5s ease-in-out;
}
.container .service button:hover i{
    transform: translateX(5px);
}