.testmonials{
    padding-top: 6rem;
    padding-bottom: 3rem;
}
.container .testmonials-container .testmonial{
    display: flex;
    align-items: center;
    /* gap: 3rem; */
margin-bottom: 2rem;
justify-content: space-around;
cursor: pointer;

}

.container .testmonials-container .testmonial h2{
    font-size: 1.4rem;
    font-weight: 600;
}
/* .container .testmonials-container .testmonial h2:hover,
