{
color: #4b8ef1;
} */
.container .testmonials-container .testmonial span{
    color: #afafaf;
    font-weight: bold;
}
.container .testmonials-container .testmonial> div{
    flex: 1;
}
.container .testmonials-container .testmonial p {
    /* color: #afafaf; */
}
.container .testmonials-container .testmonial p .awesome{
    margin-left: 5px;

}

.cto-card .client{
    background-image: url("../../images/client-bg.png");
    background-size: cover;
    background-repeat: no-repeat;
    width: 90%;
    border-radius: 3rem;
    height: 50%;
    padding: 9rem 3rem;
    color: white;
    font-weight: 600;
    position: relative;
}
.cto-card .client::before{
    content: "";
    position: absolute;
    background-image: url("../../images/quote.png");
    background-size: contain;
    background-repeat: no-repeat;
    width: 100px;
    height: 100px;
    top: 2rem;
}
.human-image{
    max-width: 170px;
    max-height: 170px;
}
@media (max-width:991px) {
    .cto-card .client {
        padding-bottom: 4rem;
        width: 70%;

    }

}
@media (max-width:575px) {
    .cto-card .client {
        padding-bottom: 4rem;
        width: 90%;
    }
.container .testmonials-container .testmonial{
        flex-direction: column;
            justify-content: center;
                align-items: center;
                text-align: center;
    }


}
.cto-card.slide{
        animation-name: slide-in;
        animation-duration: 0.5s;
        animation-iteration-count: 1;
        
}
@keyframes slide-in {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

